.Intro{
    background: url("../../assets/resort.jpg");
    background-size: 100%;
    background-repeat: no-repeat;    
    height: 110vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.inner{
    
    background-color: rgba(49, 49, 49, .8);
    width: 60%;
    height: 40%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.inner_header{
    
    font-size: 3rem;
    color:rgb(223, 223, 223);
    text-transform: uppercase;
    letter-spacing: .3rem;
}
.inner_link{
    text-transform: uppercase;
    text-decoration: none;
    color: rgb(223, 223, 223);
    border: 2px solid var(--color-primary);
    padding: 1.2rem 3.7rem;
    transition: .2s all;
    letter-spacing: .2rem;
    
}
.inner_link:hover{
    background-color: var(--color-primary);
    color: rgb(49, 49, 49);
}
.inner_linktext{
    font-size: 2rem;
    font-weight: 200;
}
@media only screen and (max-width: 600px) {
    html{
        font-size: 45%;

        overflow-x: hidden;

    }
    body{
        overflow-x: hidden;
    }
    
    .Intro{
        font-size: 1.3rem;
        height: 40vh;
        background-size: auto 40vh;
    }
    .inner{
        height:20vh;
    }
    .inner_header{
        font-size: 2.4rem;
    }
    .inner_link{
        font-size: 1.8rem;
    }
}
