.rooms_container{
    margin-bottom: -8rem;
}
.rooms{
    background-color: whitesmoke;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
}
.card{
    
  height: 55vh;
  margin-top: 20rem;
  margin-bottom: 10rem;
  -ms-flex-preferred-size: 19%;
      flex-basis: 19%;
  color: #252525;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-radius: 7px;
  -webkit-transition: .3s all;
  transition: .3s all;
    border: 2px solid #e5cb86;
    font-size: 1.8rem;
    margin: 0 0 !important;
    border-radius: 0 !important;
}

.card_text{

    -webkit-transition: all .3s;
    transition: all .3s;
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 2rem;
    text-align: center;
    background-color: #313131;
    border-radius: 4px;
    padding: .5rem 1.3rem;
    color: #e5cb86;
}
.card_price{
    
  -webkit-transition: all .3s;
  transition: all .3s;
  text-align: center;
  font-weight: 200;
  background-color: #313131;
  color: #e5cb86;
  border-radius: 4px;
  padding: .5rem 1.3rem;
}
.card_buy{
    opacity: 0;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    position: absolute;
    text-transform: uppercase;
    -webkit-transition: all .4s;
    transition: all .4s;
    text-align: center;
    font-weight: 200;
    background-color: #313131;
    color: #e5cb86;
    border-radius: 4px;
    padding: .7rem 2.2rem;
    font-weight: 600;
}
.card_buy:hover {
    background-color: #1d1d1d;
  }
  
  .card_buy_link {
    text-decoration: none;
    color: #e5cb86;
  }

.card:hover .card_buy {
    opacity: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  
