.heading{
    z-index: 100000;
    top:0;
    position: sticky;
    background-color: rgb(49, 49, 49);
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logobox{
    align-self: center;
    margin: 2rem 4rem;
    
}
.logo{
    height: 5rem;
}
.nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
  

.list {
  list-style-type: none;
}
.list_item {
  font-size: 1.5rem;
  display: inline-block;
  font-weight: 400;
  margin-right: 3rem;
}
.list_item:last-child {
    margin-right: 5rem;
}


.nav_link {
  text-decoration: none;
  color: #dfdfdf;
}
.nav_link:hover {
   color: #e5cb86;
   cursor: pointer;
}
@media only screen and (max-width: 600px) {
  html{
      font-size: 45%;

      overflow-x: hidden;

  }
  body{
      overflow-x: hidden;
  }
  .heading{
      font-size: 5rem;
      
  }
  .logo{
    height: 6rem;
  }
 
  .list_item{
    font-size: 2.2rem;
    margin-right: .7rem;
  }
  
  
  
  
}