html{
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body{
  font-family: 'Dosis', sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;

}
:root {
  --color-primary : rgb(229,203,134);
}
@media only screen and (max-width: 600px) {
  html{
      font-size: 45%;

      overflow-x: hidden;

  }
  body{
      overflow-x: hidden;
  }
}