.footer{
    margin-top: 12rem;
    display: flex;
    justify-content: space-around;
    background-color: rgb(48, 48, 48);
    padding-top: 5rem;
}
        
.footer_item{
    display: inline-block;
    margin-right: 1rem;
    text-transform: uppercase;
    font-weight: 100;
    font-size: 1.5rem;
}
.footer_item_link{
    text-decoration: none;
    color: rgb(136, 136, 136);
    letter-spacing: .1rem;
}
.footer_item_link:hover{
    color: #e5cb86;
}
.text{
    flex-basis: 20%;   
    color: rgb(136, 136, 136); 
    font-size: 1.3rem;
}
.logo{
    height: 5rem;
}

@media only screen and (max-width: 600px) {
  
.footer_item{
          margin-left: -4rem;
}
.footer_item_link{
                display: block;
            }
}

